<template>
  <div>
    <el-dropdown trigger="click" @command="handleReview">
      <el-button type="text" :disabled="disabled">
        <template v-if="customer.status === 'review'"><span class="text-warning"><i
          class="icon el-icon-info"></i></span></template>
        <template v-if="customer.status === 'conflict'"><span class="text-danger"><i
          class="icon el-icon-warning"></i></span></template>
        <template v-if="customer.status === 'process'"><span class="text-primary"><i
          class="icon el-icon-time"></i></span></template>
        <template v-if="customer.status === 'success'"><span class="text-success"><i
          class="icon el-icon-success"></i></span></template>
        <template v-if="customer.status === 'invalid'"><span class="text-info"><i
          class="icon el-icon-error"></i></span>
        </template>
      </el-button>
      <el-dropdown-menu slot="dropdown">
        <el-dropdown-item command="process"
                          :disabled="(customer.status === 'success' || customer.status === 'process')">
          <i class="el-icon-info text-primary"></i><span class="text-primary"></span>
        </el-dropdown-item>
        <el-dropdown-item command="conflict"
                          :disabled="(customer.status === 'conflict' || customer.status === 'success' || customer.status === 'invalid')">
          <i
            class="el-icon-warning text-danger"></i><span class="text-danger"></span>
        </el-dropdown-item>
        <el-dropdown-item command="success"
                          :disabled="(customer.status === 'success' || customer.status === 'review' || customer.status === 'invalid' || customer.status === 'conflict')">
          <i
            class="icon el-icon-success text-success"></i><span class="text-success"></span>
        </el-dropdown-item>
        <el-dropdown-item command="invalid"
                          :disabled="customer.status === 'invalid'"><i
          class="icon el-icon-error text-info"></i><span class="text-info"></span>
        </el-dropdown-item>
      </el-dropdown-menu>
    </el-dropdown>
    <el-dialog title="" :visible="dialogQuoteVisible" class="quote-dialog" :before-close="handleClose"
               width="70%" :close-on-click-modal="false">
      <div class="search" v-loading="quoteLoading" element-loading-text="...">
        <el-form inline :model="searchInput" size="mini">
          <el-form-item label="">
            <el-input placeholder="" v-model="searchInput.username" prefix-icon="el-icon-service"
                      @keyup.enter.native="searchQuotes"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input placeholder="" v-model="searchInput.phone" prefix-icon="el-icon-phone"
                      @keyup.enter.native="searchQuotes"></el-input>
          </el-form-item>
          <el-form-item label="" placeholder="">
            <el-select v-model="searchInput.status">
              <el-option label="" value="process"></el-option>
              <el-option label="" value="success"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" icon="el-icon-search" @click="searchQuotes"></el-button>
          </el-form-item>
        </el-form>
      </div>
      <el-table :data="customers" height="250" width="800">
        <el-table-column type="index" width="40"></el-table-column>
        <el-table-column label="" width="120">
          <template slot-scope="scope">
            <el-button type="text" @click="addQuote(scope.row)"
                       :disabled="(scope.row.id === customer.id) || (scope.row.status === 'conflict' || scope.row.status === 'review' || scope.row.status === 'invalid')"
                       style="padding-top: 0;padding-bottom: 0;">
              {{scope.row.username}}
            </el-button>
          </template>
        </el-table-column>
        <el-table-column label="" width="120">
          <template slot-scope="scope">
            <span v-if="scope.row.status === 'review'" class="text-warning"></span>
            <span v-if="scope.row.status === 'process'" class="text-primary"></span>
            <span v-if="scope.row.status === 'success'" class="text-success"></span>
            <span v-if="scope.row.status === 'invalid'" class="text-info"></span>
            <span v-if="scope.row.status === 'conflict'" class="text-danger"></span>
          </template>
        </el-table-column>
        <el-table-column property="location" label="" width="120"></el-table-column>
        <el-table-column property="phone" label="" width="150"></el-table-column>
        <el-table-column property="created_at" label=""></el-table-column>
        <el-table-column label="" width="120">
          <template slot-scope="scope">
            <el-button type="text" @click="addQuote(scope.row)"
                       :disabled="(scope.row.id === customer.id) || (scope.row.status === 'conflict' || scope.row.status === 'review' || scope.row.status === 'invalid')"
                       style="padding-top: 0;padding-bottom: 0;">
            </el-button>
          </template>
        </el-table-column>
      </el-table>
      <span slot="footer" class="dialog-footer">
          <el-pagination :page-size="15" layout="total, prev, pager, next"
                         :total="total" @current-change="changePage" class="dialog-page"></el-pagination>
      </span>
    </el-dialog>
    <el-dialog title="" :visible="noteDialogVisible" width="30%" class="note-dialog" top="28vh" lock-scroll
               :close-on-click-modal="false" :before-close="handleCloseNoteDialog">
      <div style="margin-bottom: 10px">{{text}}</div>
      <label></label>
      <el-input type="textarea" :placeholder="`${text}`" v-model="customer.admin_note"
                style="margin-bottom:10px;"></el-input>
      <label></label>
      <el-input type="textarea" :placeholder="`${text}${text}`"
                v-model="customer.invalid_progress"
                style="margin-bottom:10px;"></el-input>
      <el-button type="text" @click="quote" v-if="command === 'conflict'"></el-button>
      <div>
        <el-tag v-if="conflict" closable @close="removeConflict">{{conflict.username}}</el-tag>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="noteDialogVisible = false" size="small"> </el-button>
        <el-button type="primary" @click="addNote" size="small"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
  import customer from '../api/customer'
  import Project from '../api/project'
  import Utils from '../common/utils'
  export default {
    props: ['customer', 'disabled'],
    data() {
      return {
        dialogQuoteVisible: false,
        noteDialogVisible: false,
        quoteLoading: false,
        customers: [],
        text: '',
        searchInput: {},
        conflict: null,
        page: 1,
        total: 0,
        command: ''
      }
    },
    methods: {
      async handleReview(command) {
        let text = ''
        switch (command) {
          case 'process':
            text = ''
            break
          case 'success':
            text = ''
            break
          case 'invalid':
            text = ''
            break
          case 'conflict':
            text = ''
            break
        }
        this.text = text
        this.command = command
        if (command === 'process' || command === 'success') {
          await this.$confirm(`${text}`, '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          })
        }
        try {
          if (command === 'conflict' || command === 'invalid') {
            this.noteDialogVisible = true
          } else {
            this.loading = this.$loading({
              lock: true,
              text: '',
              spinner: 'el-icon-loading',
              background: 'rgba(255, 255, 255, 0.7)'
            })
            const {data} = await customer.review(this.customer.id, command)
            this.customer.status = data.status
            this.$emit('update:row', command)
            this.loading.close()
            this.$message({
              type: 'success',
              showClose: true,
              dangerouslyUseHTMLString: true,
              message: `<strong>${text}</strong>`
            })
          }
        } catch (e) {
          console.error(e)
          this.loading.close()
          this.$message({
            type: 'error',
            showClose: true,
            dangerouslyUseHTMLString: true,
            message: ` ${e.errmsg}`
          })

        }
      },
      //
      async quote() {
        const loading = this.$loading({
          target: document.querySelector('.quote-dialog'),
          text: '...',
          fullscreen: false
        })
        this.dialogQuoteVisible = true
        try {
          this.status = ['process', 'success']
          const {data, total} = await customer.search({
            status: this.status
          })
          if (data) {
            this.customers = data.map(v => {
              v.location = Utils.parseLocation(v.location)
              return v
            })
            this.total = total
          } else {
            this.customers = []
          }
        } catch (e) {
        }
        loading.close()
      },
      addQuote(customer) {
        this.conflict = customer
        this.dialogQuoteVisible = false
      },
      //
      async searchQuotes() {
        const loading = this.$loading({
          target: document.querySelector('.quote-dialog'),
          text: '...',
          fullscreen: false
        })
        this.status = [this.searchInput.status]
        const {data, total} = await customer.search({
          phone: this.searchInput.phone,
          username: this.searchInput.username,
          status: this.searchInput.status,
          page: this.page
        })
        if (data) {
          this.customers = data.map(v => {
            v.location = Utils.parseLocation(v.location)
            return v
          })
          this.total = total
        } else {
          this.customers = []
        }
        loading.close()
      },
      async changePage(page) {
        const loading = this.$loading({
          target: document.querySelector('.quote-dialog'),
          text: '...',
          fullscreen: false
        })
        try {
          const {data, total} = await customer.search({
            phone: this.searchInput.phone,
            username: this.searchInput.username,
            status: this.status,
            page
          })
          if (data) {
            this.customers = data.map(v => {
              v.location = Utils.parseLocation(v.location)
              return v
            })
            this.total = total
          } else {
            this.customers = []
          }
        } catch (e) {
        }
        loading.close()
      },
      removeConflict() {
        this.conflict = null
      },
      handleClose() {
        this.dialogQuoteVisible = false
      },
      handleCloseNoteDialog() {
        this.noteDialogVisible = false
      },
      async addNote() {
        if (this.command === 'conflict') {
          try {
            if (this.customer.admin_note && this.customer.invalid_progress) {
              if (!this.conflict) {
                this.$message.error('')
                return
              }
              await customer.addConflictRelations({
                id: this.$route.params.id,
                conflict_id: this.conflict.id,
                note: this.customer.admin_note
              })
              await Project.addProgress(this.customer.project_id, this.customer.invalid_progress)
              this.$message({
                type: 'success',
                message: `${this.customer.username}`
              })
              this.customer.status = 'conflict'
              this.$emit('addProgress')
              this.noteDialogVisible = false
            }
          } catch (e) {
          }
        } else if (this.command === 'invalid') {
          try {
            if (this.customer.admin_note && this.customer.invalid_progress) {
              await customer.review(this.customer.id, 'invalid')
              await customer.addNote(this.customer.id, this.customer.admin_note)
              //
              await Project.addProgress(this.customer.project_id, this.customer.invalid_progress)
              this.$message({
                type: 'success',
                message: `${this.customer.username}`
              })
              this.customer.status = 'invalid'
              this.$emit('addProgress')
              this.noteDialogVisible = false
            }
          } catch (e) {
          }
        }

      }
    }
  }
</script>
<style lang="scss">
  .note-dialog {
    .el-dialog__header {
      padding: 15px 15px 0;
    }
    .el-dialog__body {
      padding: 0 20px;
    }
    .el-dialog__footer {
      padding: 0 15px 10px;
    }
  }
</style>



